import {App} from "vue";
import {Router} from "vue-router";
import tailwindConfig from '../../tailwind.config';
import tailwindResolveConfig from 'tailwindcss/resolveConfig';
import {Theme} from "@/viewer/types";

export default {
    install: (app: App, router: Router) => {
        const {theme}: { theme: Theme } = tailwindResolveConfig(tailwindConfig);

        router.addRoute({
            path: '/_tailwind',
            name: '_tailwind',
            component: () => import('./viewer.vue'),
            meta: {theme, baseFontSize: 16}
        });
    }
}
