export const namespaced = true;

interface User {
    id: number;
}

type State = {
    user: User;
};

export const state = () => ({
    user: {},
});

export const getters = {
    user(state: State) {
        return state.user;
    },
};

export const mutations = {
    SET_USER(state: State, data: User) {
        state.user = data;
    },
};

export const actions = {
    login({commit}: any) {
        commit('SET_USER', {id: 1});
    },
};
